import request from '../utils/request.js'

// 
export const getHome = () => {
	return request({
		url:'/home/index'
	})
}

// 
export const getHead = () => {
	return request({
		url:'/home/category/head'
	})
}

// 
export const getBanner = () => {
	return request({
		url:'/home/banner'
	})
}

// 
export const getNew = () => {
	return request({
		url:'/home/new'
	})
}

// 
export const getHot = () => {
	return request({
		url:'/home/hot'
	})
}

// 
export const getBeand = () => {
	return request({
		url:'/home/brand'
	})
}

// 
export const getBrandId = (id) => {
	return request({
		url:`/brand/${id}`
	})
}
