import request from '../utils/request.js'

export const login = (data) => {
	return request({
		url:'/login',
		method:'POST',
		data
	})
}

// 
export const getUserInfo = () => {
	return request({
		url:'/member/profile'
	})
}

// 
export const editUserInfo = (data) => {
	return request({
		url:'/member/profile',
		method:'PUT',
		data
	})
}

// 
export const editAvatar = (data) => {
	return request({
		url:' /member/profile/avatar',
		method:'POST',
		data
	})
}