
// 
export const setStorage = (key,data) => {
	uni.setStorage({
		key:key,
		data:JSON.stringify(data)
	})
}

// 
export const getStorage = (key) => {
	try{
		const value = JSON.parse(uni.getStorageSync(key))
		return value
	}catch(e){
		console.log('key!');
		console.log(e);
	}
}

// 
export const removeStorage = (key) => {
	uni.removeStorageSync(key)
}
